import React, { Component } from 'react';
import { connect } from 'react-redux';
import  {getNews}  from '../actionCreator/newsAction';
import "./newContent.css";
class Newscontent extends Component {
    componentDidMount(){
        this.props.getNews("general");
    }
    goToTheNew = (id)=>{
        window.open(this.props.news[id].url,"_blank")
    }
    render() {
        return (
          <React.Fragment>
            <section className="mt-3 pt-4">
                <div className="container">
                    <div className="row">
                        {this.props.news.map((item,index)=>{
                            return(
                                <div key = {index} className = "col-lg-4 col-md-12 my-2" onClick = {()=>this.goToTheNew(index)}>
                                    <div className="news-item card text-center pb-3 my-3">
                                        <div className = "image">
                                            <img src = {item.urlToImage}  className="w-100" alt=""/>
                                        </div>
                                        <span>{item.title}</span>
                                        <p>{item.description}</p>
                                    </div>
                                </div>
                            )
                        })}
                    </div>
                </div>
            </section>
          </React.Fragment>
        );
    }
}
function mapStateToProps(state){
    return{
        news : state.news,
        name : state.name
    }
}
export default connect(mapStateToProps,{getNews})(Newscontent);