import React, { Component } from 'react';
import { connect } from 'react-redux';
import  {currentLink}  from '../actionCreator/actions';
import "./header.css";
import  {getNews}  from '../actionCreator/newsAction';

class Header extends Component {
    render() {
        return (
            <React.Fragment>
                <header className = "position-fixed w-100">    
                        <nav className="navbar navbar-expand-lg myBackground">
                            <div className="container">
                                <a className="navbar-brand link-color" href="/">اخبار مصر</a>
                                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                                    <span className="navbar-toggler-icon btn-toggler-icon"></span>
                                    <span className="navbar-toggler-icon btn-toggler-icon"></span>
                                    <span className="navbar-toggler-icon btn-toggler-icon"></span>
                                </button>

                                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                                    <ul className="navbar-nav mr-auto">
                                        <li className="nav-item">
                                            <a className="nav-link link-color" name = "general" onClick={(e)=>{
                                                e.preventDefault();
                                                this.props.currentLink(e.target.name)
                                                this.props.getNews(e.target.name)
                                            }} href="#general">الرئيسية</a>
                                        </li>
                                        <li className="nav-item">
                                            <a className="nav-link link-color" name = "business" onClick={(e)=>{
                                                e.preventDefault();
                                                this.props.currentLink(e.target.name)
                                                this.props.getNews(e.target.name)
                                            }} href="#business">اخبار الاعمال</a>
                                        </li>
                                        <li className="nav-item">
                                            <a className="nav-link link-color" name = "health" onClick={(e)=>{
                                                e.preventDefault();
                                                this.props.currentLink(e.target.name)
                                                this.props.getNews(e.target.name)
                                            
                                            }} href="#health">اخبار الصحة</a>
                                        </li>
                                        <li className="nav-item">
                                            <a className="nav-link link-color" name = "sports" onClick={(e)=>{
                                                e.preventDefault();
                                                this.props.currentLink(e.target.name)
                                                this.props.getNews(e.target.name)
                                            }} href="#sports">اخبار الرياضة</a>
                                        </li>
                                        <li className="nav-item">
                                            <a className="nav-link link-color" name = "technology" onClick={(e)=>{
                                                e.preventDefault();
                                                this.props.currentLink(e.target.name)
                                                this.props.getNews(e.target.name)
                                            }} href="#technology">اخبار التكنولوجيا</a>
                                        </li>
                                    </ul>
                                </div>
                            </div>
                        </nav>
                </header>
            </React.Fragment>
        );
    }
}
function mapStateToProps(state){
    return{
        name : state.name ,
        news : state.news
    }
}
export default connect(mapStateToProps,{currentLink , getNews})(Header);